<template>
  <BaseLayout title="员工">
    <template #extra>
      <van-button icon="add" :style="{width:'110px', borderRadius:'10px 0 0 10px', color: '#000'}" color="#F6F6F6" @click="onEdit(null)">新增员工</van-button>
    </template>

    <div class="toolbar">
      <van-search v-model="state.keyword" placeholder="请输入手机号或员工姓名" clearable  @search="onRefresh" @clear="onRefresh" />
    </div>

    <van-pull-refresh v-model="state.refreshing" @refresh="onRefresh">
      <van-list class="list" v-model:loading="state.loading" :finished="state.finished" loading-text="正在请求数据 ..." finished-text="没有更多了" :immediate-check="false" @load="onLoad">
        <van-contact-card type="edit" class="item" v-for="(item, index) in state.list" :key="index" :name="item.truename" :tel="item.mobile" @click="onEdit(item)"/>
        <van-empty v-show="state.list.length == 0" :style="{width:'100%'}"></van-empty>
      </van-list>
    </van-pull-refresh>

    <van-action-sheet v-model:show="state.edit" :title="state.title">
      <van-contact-edit :is-edit="state.del" :contact-info="state.info" @save="onSave" @delete="onDelete" />
    </van-action-sheet>

  </BaseLayout>
</template>

<style scoped>
.list {
  min-height: 500px;
}
.item {
  margin-top: 10px;
  background: #FFF;
  overflow:hidden;
}
</style>

<script>
import {reactive} from 'vue'
import BaseLayout from '@/components/BaseLayout.vue'

export default {
  components: {BaseLayout},

  setup() {
    const state = reactive({
      refreshing: false,
      loading: false,
      finished: false,

      list: [],
      keyword: "",
      offset: 0,
      limit : 20,

      edit: false,
      del: false,
      title: "",
      info: {},
    });
    return {state};
  },

  created() {
    this.onRefresh();
  },

  methods: {
    onRefresh() {
      this.state.edit = false;
      
      this.state.loading = true;
      this.state.finished = false;
      this.state.offset = 0;
      this.state.list = [];
      this.onLoad();
    },
    onLoad() {
      this.$http.get("/team/memberList?keyword=" + this.state.keyword + "&offset=" + this.state.offset + "&limit=" + this.state.limit).then((data) => {
        this.state.refreshing = false;
        if (data.length == 0 || data.length < this.state.limit)
          this.state.finished = true;
        this.state.loading = false;
        this.state.offset += data.length;

        for (let i in data){
          let item = data[i];
          this.state.list.push({...item, ...{
            amount: item.cash > 0 ? item.cash : item.point,
          }});
        }
      });
    },
    onEdit(item) {
      this.state.del = false;
      this.state.title = "新增员工";
      this.state.info = {};
      if (item != null) {
        this.state.del = true;
        this.state.title = "员工管理";
        this.state.info = {id:item.id, name: item.truename, tel: item.mobile};
      }
      this.state.edit = true;
    },
    onSave(item) {
      this.$http.post("/team/memberSave?" + (item.id ? "id=" + item.id : "") + "&truename=" + item.name + "&mobile=" + item.tel).then(this.onRefresh);
    },
    onDelete(item) {
      this.$http.post("/team/memberSave?id=" + item.id + "&remove=1").then(this.onRefresh);
    },
  }
}
</script>
